        { text: "4G", value: 1 }
      ],
      stageList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 },
        { text: "", value: 4 },
        { text: "", value: 5 }
      ],
      issueCategoryList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 }
      ],
      menuList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 },
        { text: "", value: 4 }
      ],
      phoneStatusList: [
        { text: "", value: 0 },
        { text: ":", value: 1 },
        { text: ":", value: 2 },
        { text: ":", value: 3 },
        { text: ":", value: 4 },
        { text: ":", value: 5 },
        { text: ":", value: 6 },
        { text: ":power", value: 7 },
        { text: ":", value: 8 }
      ],
      phoneSoundCategoryList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 }
      ],
      PhoneSoundStatusList: [
        { text: "()", value: 0 },
        { text: "()", value: 1 },
        { text: "()", value: 2 },
        { text: "()", value: 3 },
        { text: "()", value: 4 },
        { text: "()", value: 5 },
        { text: "()", value: 6 },
        { text: "()", value: 7 },
        { text: "()", value: 8 },
        { text: "()", value: 9 },
        { text: "()", value: 10 },
        { text: "()", value: 11 },
        { text: "()", value: 12 },
        { text: "()", value: 13 }
      ],
      microphoneStatusList: [
        { text: "", value: 0 },
        { text: "()", value: 1 },
        { text: "", value: 2 }
      ],
      microphoneCategoryList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 }
      ],
      phoneCameraStatusList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "APP", value: 2 }
      ],
      phoneStorageStatusList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "APP", value: 2 }
      ],
      phoneElectricityStatusList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 }
      ],
      phoneScreenOrientationLockList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 }
      ],
      phoneScreenOpenStatusList: [
        { text: "Power", value: 0 },
        { text: "", value: 1 }
      ],
      phoneGestureActionList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 }
      ],
      systemList: [{ text: "IOS", value: 0 }, { text: "Android", value: 1 }],
      failRateList: [{ text: "", value: 0 }, { text: "", value: 1 }],
      fileList: []
    };
  },

  methods: {
    clickCreateIssue() {
      if (this.checkNullString(this.username)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (this.checkNullString(this.version)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (this.checkNullString(this.phoneModel)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (this.checkNullString(this.phoneIos)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (this.checkNullString(this.phoneBrand)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }

      if (isNaN(this.level_value)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (isNaN(this.clazz_value)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (isNaN(this.network_status_value)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (this.checkNull(this.network_category_value)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (this.checkNull(this.stage_value)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (this.checkNull(this.issue_category_value)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (this.checkNull(this.menu_value)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (this.checkNull(this.fail_rate_value)) {
        this.$toast.fail({
          message: "",
          forbidClick: true,
          closeOnClick: true,
          duration: 1000
        });
        return;
      }
      if (this.fileList != null && this.fileList.length > 0) {
        //fileList
        var hasImage = false;
        var hasVideo = false;
        var hasOtherFile = false;
        for (var i = 0; i < this.fileList.length; i++) {
          let file = this.fileList[i];
          if(parseInt(file.file.size)>=parseInt(20971520)){
            this.$toast("20M");
            return;
          }
          if (file.file.type == "" || file.file.type == undefined) {
            this.$toast("");
            return;
          }
          switch (file.file.type) {
            case "video/mp4":
              hasVideo = true;
              break;
            case "video/quicktime":
              hasVideo = true;
              break;
            case "image/png":
              hasImage = true;
              break;
            case "image/jpeg":
              hasImage = true;
              break;
            case "image/heic":
              hasImage = true;
              break;
            default:
              hasOtherFile = true;
              break;
          }
        }
        if (hasOtherFile) {
          this.$toast("");
          return;
        }
        this.uploadFile();
      } else {
        this.createIssue("");
      }
    },
    initDefaultData() {
      this.phoneIos = localStorage.getItem("phoneIos");
      this.phoneModel = localStorage.getItem("phoneModel");
      this.username = localStorage.getItem("username");
      this.phoneBrand = localStorage.getItem("phoneBrand");
      var num = parseInt(localStorage.getItem("system_value"));
      this.system_value = isNaN(num) ? 0 : num;
      this.level_value = parseInt(localStorage.getItem("level"));
      this.clazz_value = parseInt(localStorage.getItem("clazz"));
      this.network_status_value = parseInt(
        localStorage.getItem("network_status_value")
      );
      var user = JSON.parse(sessionStorage.getItem("user"));
      if (user != null) {
        this.userId = user.userId;
      }
    },
    saveDefaultData() {
      localStorage.setItem("phoneIos", this.phoneIos);
      localStorage.setItem("phoneModel", this.phoneModel);
      localStorage.setItem("phoneBrand", this.phoneBrand);
      localStorage.setItem("system_value", this.system_value);
      localStorage.setItem("username", this.username);
      localStorage.setItem("level", this.level_value);
      localStorage.setItem("clazz", this.clazz_value);
      localStorage.setItem("network_status_value", this.network_status_value);
    },
    createIssue(mediaUrlsText) {
      var readyData = {
        clazz: this.clazz_value,
        category: this.issue_category_value + 1,
        creator: this.userId,
        version: this.version,
        phoneBrand: this.phoneBrand,
        phoneIos: this.phoneIos,
        phoneModel: this.phoneModel,
        testComments: this.test_comments,
        phoneStatus: this.phone_status_value,
        phoneSoundStatus: this.Phone_sound_status_value,
        phoneSoundCategory: this.phone_sound_category_value,
        microphoneStatus: this.microphone_status_value,
        microphoneCategory: this.microphone_category_value,
        phoneNetworkStatus: this.network_status_value,
        phoneNetworkCategory: this.network_category_value + 1,
        phoneCameraStatus: this.phone_camera_status_value,
        phoneStorageStatus: this.phone_storage_status_value,
        phoneElectricityStatus: this.phone_electricity_status_value,
        //
        phoneScreenResolution:
          parseInt(screen.availWidth * window.devicePixelRatio) +
          "*" +
          parseInt(screen.availHeight * window.devicePixelRatio),
        phoneScreenOrientationLock: this.phone_screen_orientation_lock_value,
        phoneScreenOpenStatus: this.phone_screen_open_status_value,
        phoneGestureAction: this.phone_gesture_action_value,
        mediaUrls: mediaUrlsText,
        failRate: this.fail_rate_value + 1,
        mobile: this.username,
        level: this.level_value,
        menu: this.menu_value + 1,
        stage: this.stage_value + 1,
        system: this.systemList[this.system_value].text
      };
      this.showLoading();
      this.axios
        .post("/createIssue", readyData)
        .then(res => {
          if (res.data.status == 0) {
            this.$notify({
              type: "success",
              message: "",
              duration: 0,
              onClick() {
                this.$notify.clear();
              }
            });
            this.saveDefaultData();
            //
            this.version = "";
            this.stage_value = null;
            this.network_category_value = null;
            this.issue_category_value = null;
            this.menu_value = null;
            this.fail_rate_value = null;
            this.test_comments = "";
            this.fileList = [];
          } else {
            this.$notify({
              type: "danger",
              message: res.data.msg,
              duration: 0,
              onClick() {
                this.$notify.clear();
              }
            });
          }
          this.hideLoading();
        })
        .catch(err => {
          this.hideLoading();
          console.error(err);
        });
    },
    uploadFile() {
      this.showLoading();
      let fd = new FormData();
      if (this.fileList && this.fileList.length) {
        //  
        this.fileList.forEach(item => {
          fd.append("files", item.file); //file
        });
      } else {
        fd.append("files", this.fileList.file);
      }
      const axiosAjax = this.axios.create({
        timeout: 1000 * 60, //
        withCredentials: true //cookie
      });
      axiosAjax
        .post("/file_upload", fd, {
          "Content-Type": "multipart/form-data"
        })
        .then(res => {
          if (res.data.status == 0) {
            this.$notify({ type: "success", message: "" });
            this.createIssue(res.data.data);
          } else {
            this.$notify({ type: "danger", message: "" });
          }
          this.hideLoading();
        })
        .catch(err => {
          this.hideLoading();
          console.error(err);
        });
    },
    showLoading() {
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
        mask: true
      });
    },
    hideLoading() {
      this.$toast.clear();
    },
    checkNull(value) {
      if (value == null) {
        return true;
      }
      return false;
    },
    checkNullString(value) {
      if (value == null || value == undefined || value == "") {
        return true;
      }
      return false;
    }
  },
  created() {
    this.initDefaultData();
  }
};
</script>
<style lang="less">
.van-col {
  font-size: 14px;
  padding: 5px;
}
.dropdown_text {
  text-align-last: center;
  padding-top: 18px !important;
}
.submit_message_text {
  text-align: center;
  font-weight: bold;
  padding: 8px;
}
.border_class {
  border: 1px solid #ccc !important;
  border-radius: 16px !important;
}
.borderBottomInput {
  .van-cell__value {
    border-bottom: #666 1px solid;
  }
}
</style>


